"use client";
import React from 'react';

type AccordionProps = {
    header: string,
    content: string;
};

const Accordion = ({ header, content }: AccordionProps) => {
    // Prevent scroll-into-view on input focus/click
    const inputRef = React.useRef<HTMLInputElement>(null);

    React.useEffect(() => {
        const input = inputRef.current;
        if (!input) return;
        // Prevent scrollIntoView on focus
        const handleMouseDown = (e: MouseEvent) => {
            e.preventDefault();
            input.checked = !input.checked;
            // Manually trigger change event if needed
            input.dispatchEvent(new Event('change', { bubbles: true }));
        };
        input.addEventListener('mousedown', handleMouseDown);
        return () => {
            input.removeEventListener('mousedown', handleMouseDown);
        };
    }, []);

    return (
        <div className="collapse bg-base-200 border border-base-300">
            <input ref={inputRef} type="radio" name="accordion" tabIndex={-1} />
            <div className="collapse-title font-semibold">{header}</div>
            <div className="collapse-content text-sm">{content}</div>
        </div>
    );
};

export default Accordion;